import React from 'react';
import './Card.css';

const Card = () => (
  <div className="card-container">
    This is one card. 
    <button>
      <i className="fa fa-times"/>
    </button>
    <i className="fa fa-hand-grab-o"/>
  </div>
);

export default Card;



